.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-logo {
  width: 0.98em;
  height: 1em;
  fill: initial;
}

body {
  font-size: 18px;
  background: #FFF;
  line-height: 1.4;
}

.block {
  display: flex;
}

.button {
  font-size: 18px;
  color: #fff;
  background-color: #1d354e;
  text-decoration: none;
  padding: 22px 68px;
  border-radius: 50px;
}

.button:hover {
  background-color: rgba(29, 53, 78, 0.7);
}

.button:active {
  background-color: rgba(29, 53, 78, 0.85);
}

.button--border-middle {
  border-radius: 20px;
  background-color: #fff;
  color: #1d354e;
  padding: 10px 25px;
  font-size: 16px;
}

.button--border-middle:hover {
  background: rgba(255, 255, 255, 0.7);
}

.button--border-middle:active {
  background: rgba(255, 255, 255, 0.85);
}

.header {
  font-family: "Montserrat", Helvetica, sans-serif;
  color: #fff;
  background-color: #00bdca;
  padding: 64px 0 115px;
}

.header__inner {
  font-size: 0;
  margin: auto;
  max-width: 996px;
  display: flex;
  justify-content: space-between;
}

.header__menu {
  display: flex;
  align-items: center;
}

.header__login {
  margin: 0 8px;
  font-weight: 600;
  font-size: 16px;
}

.logo {
  font-family: "Montserrat", Helvetica, sans-serif;
  display: flex;
}

.logo__image {
  display: flex;
  align-items: center;
}

.logo__image .icon {
  width: 43px;
  height: 44px;
  fill: #fff;
}

.logo__text {
  margin-left: 12px;
}

.logo__title {
  font-weight: 600;
  font-size: 24px;
}

.logo__title--cyan {
  color: #56ecf6;
}

.logo__description {
  line-height: 0.5;
  font-weight: 400;
  font-size: 13px;
}

.nav {
  font-family: "Montserrat", Helvetica, sans-serif;
}

.nav__list {
  display: flex;
}

.nav__item {
  margin: 0 17px;
}

.nav__item:last-child {
  margin-right: 25px;
}

.nav__link {
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  font-size: 16px;
}

.nav__link:hover {
  text-decoration: underline;
}

.nav__link:active {
  color: rgba(255, 255, 255, 0.7);
}

.lang {
  font-family: "Montserrat", Helvetica, sans-serif;
  position: relative;
}

.lang__current {
  padding: 10px 24px;
  font-size: 16px;
  color: transparent;
}

.lang__list {
  padding: 10px 24px;
  background-color: #fff;
  border-radius: 20px;
  left: 0;
  top: 0;
  position: absolute;
}

.lang__list:hover .lang__item--hidden {
  display: block;
}

.lang__item {
  font-size: 16px;
}

.lang__item:hover {
  text-decoration: underline;
}

.lang__item--hidden {
  display: none;
}

.lang__link {
  text-decoration: none;
  color: #1d354e;
  font-weight: 600;
  font-size: 16px;
}

.hero {
  overflow: hidden;
  position: relative;
  height: 1300px;
  background-color: #1d354e;
}

.hero:before {
  content: "";
  top: -50%;
  position: absolute;
  width: 100%;
  height: 100%;
  transform: skewY(10deg);
  background-color: #00bdca;
}

.hero__inner {
  max-width: 1170px;
  margin: auto;
}

.hero__text {
  display: flex;
  max-width: 996px;
  margin: auto;
  position: relative;
  height: 578px;
}

.hero__image {
  z-index: 1;
  position: absolute;
  height: 780px;
  top: -36px;
  left: -144px;
}

.hero__title-wrapper {
  z-index: 1;
  font-family: "Montserrat", Helvetica, sans-serif;
  color: #fff;
  margin-left: auto;
  text-align: center;
  width: 62%;
}

.hero__title {
  font-size: 72px;
}

.hero__description {
  font-size: 30px;
}

.hero__button {
  display: inline-block;
  margin-top: 10px;
}

.hero__video {
  position: relative;
  display: flex;
  align-items: center;
  height: 722px;
  background: #fff;
  border-radius: 10px;
}

.hero__video-container {
  display: flex;
  width: 1080px;
  height: 624px;
  margin: auto;
  background: #000;
  border-radius: 10px;
}

.hero__video-play {
  width: 120px;
  height: 120px;
  margin: auto;
  background: url("../../img/play-button.svg") no-repeat center;
  background-size: cover;
}

.hero__video-play:hover {
  opacity: 0.7;
}

.hero__video-play:active {
  opacity: 0.85;
}
