//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

// Montserrat
@font-face {
	@include fontdefault(normal, 200, 'Montserrat');
	@include fontpath('Montserrat Extra-Light', 'Montserrat-ExtraLight', Montserrat-ExtraLight); }
@font-face {
	@include fontdefault(normal, 300, 'Montserrat');
	@include fontpath('Montserrat Light', 'Montserrat-Light', Montserrat-Light); }
@font-face {
	@include fontdefault(normal, 400, 'Montserrat');
	@include fontpath('Montserrat', 'Montserrat-Regular', Montserrat-Regular); }
@font-face {
	@include fontdefault(normal, 600, 'Montserrat');
	@include fontpath('Montserrat Semi-Bold', 'Montserrat-SemiBold', Montserrat-SemiBold); }
@font-face {
	@include fontdefault(normal, 700, 'Montserrat');
	@include fontpath('Montserrat Bold', 'Montserrat-Bold', Montserrat-Bold); }
@font-face {
	@include fontdefault(normal, 800, 'Montserrat');
	@include fontpath('Montserrat Extra-Bold', 'FuturaFuturis-Extrabold', Montserrat-ExtraBold); }
